#secondary .social-icons {
	a {
		img {
			margin-right: 10px;
			opacity: 0.8;
		}
		&:hover, &:active, &:focus {
			img {
				opacity: 1;
			}
		}
	}
}

.entry-content {
	h2 { font-size: 35px; }
	h3.event-title { margin-top: 0; }
}

.page-id-59 {
	.entry-content {
		h2 {
			font-size: 28px;
			line-height: 1.5;
			margin-bottom: 20px;
		}
		img { box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14); }
	}
}

.page .entry-content {
	@media (max-width: 768px) {
		.alignright, .alignleft {
			float: none;
			margin: 0 auto;
		}
	}
}

.uw-hero-image {
	&.opl-header1 {
		background-image: url(../../assets/headers/opl/meany-stage-011.jpg);
		background-position: top center;
	}

	&.opl-header2 {
		background-image: url(../../assets/headers/opl/meany-stage-064.jpg);
		background-position: bottom center;
	}

	&.opl-header3 {
		background-image: url(../../assets/headers/opl/meany-seats-053.jpg);
		background-position: bottom center;
	}

	&.opl-header4 {
		background-image: url(../../assets/headers/opl/meany-stage-side.jpg);
		background-position: center center;
	}
}

.map-responsive {
	overflow: hidden;
	padding-bottom: 56.25%;
	position: relative;
	height: 0;
}

.map-responsive iframe {
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	position: absolute;
}
